*{margin: 0;padding: 0;}

body{
  font-family:'Karla', serif;
  color:#333333;
  font-size: 1em;
}

/* Navigation */


nav{
  font-family:'Markazi Text', serif;
  font-size: 1.5em;
  display: flex;
  justify-content: space-between;
  max-width: 500px;
  margin:0 auto;
}

nav ul li{
  list-style: none;
}

nav ul li a{
  text-decoration: none;
}

.menu-icon{
  width: 1.5em;
  margin: 0.25em;
}

.nav-links-menu{
  position: absolute;
  top: 2em;
  background-color: white;
  border: 1px solid gray;
}

.nav-links-menu li{
  margin-left: 0.3em;
  margin-right: 0.5em;
}

.nav-links{
  display: none;
}

#nav-logo{
  height: 1.5em;
  margin: 0.25em;
}

/* Hero */


.hero-container{
  background-color: #495E57;
}

.hero{
  color:#EDEFEE;
  display: flex;
  max-width: 500px;
  margin:0 auto;
  justify-content: space-between;
}

.hero-description{
  display: flex;
  flex-direction: column;
}
.hero-description>*{
  margin-left:10px ;
}


.hero *{
  /* border:1px solid red; */
}

.hero p{
  max-width: 200px;
  min-width: 130px;
}

.hero-text{

}

h1{
  color:#F4CE14;
  font-size: 2em;
  margin-top: 0.25em;
}

.subtitle{
  font-size: 1.5em;
  margin-bottom: 0.5em;
}

.btn-primary{
  width: 55%;
  height: 2em;
  border-radius: 10px;
  background-color:#F4CE14;
  font-weight: 700;
  color:#000000;
  min-width: 120px;
  margin-top: 2em;
  margin-bottom: 1em;
  border:none;
}

.hero-image{
  width: 35%;
  border-radius: 16px;
  min-width: 150px;
  margin: 1em;
}

/*Specials*/

.specials-header{
  display: flex;
  margin:0 auto;
  max-width: 500px;
  justify-content: space-between;
  align-items: center;
  padding-top: 1em;
  padding-bottom:0.5em ;
}
.specials-header>*{
  margin: 0 0.85em;

}

.specials-header button{
  width: auto;
}

.dish-list{
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 500px;
  margin: 0 auto;
  padding-bottom: 0.5em;
}


.dish-list>*{
  margin:0.5em 1em;
}

.dish-card{
  background-color:#EDEFEE;
  display: flex;
  flex-direction: column;
  border-radius: 16px 16px 16px 16px;
  border: 1px solid rgb(215, 212, 212);
}

.dish-image{
  width: 100%;
  border-radius: 16px 16px 0 0;
}

.dish-info{
  display: flex;
  flex-direction: column;
  padding: 0.5em;
}

.dish-info>*{
  margin-top: 0.5em;
}

.dish-header{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.dish-price{
  font-weight: 700;
  color:#EE9972 ;
}

button:hover{
  background-color:#EE9972 ;
}

/*footer*/

.footer{
  background-color: #495E57;
  height: 50px;
  display: flex;
  padding-top: 1em;
  justify-content: center;
  color:#EDEFEE;
}

@media (min-width: 800px) {

  nav{
    max-width: 1000px;
    margin:0 auto;
  }

  .menu-icon{
    display: none;
  }

  .nav-links, .nav-links-menu{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width:100%;
  }

  .specials-header{
    max-width: 1000px;
  }
  .dish-list{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    align-items: start;
    max-width: 1000px;

  }

  .dish-card{
    height: 400px;
  }

  .footer{
    height: 70px;
  }
}